.card-resultado-semana {
  width: 100% !important;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 10px;
  margin-bottom: 30px;
}
.card-resultado-semana .row {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.card-resultado-semana .row p {
  margin-left: 10px;
}
.card-resultado-semana header {
  margin-bottom: 10px;
  width: 100%;
}
.card-resultado-semana header span {
  display: inline-block;
  width: 15px;
  height: 15px;
  background: #66e929;
  border-radius: 50%;
}
.card-resultado-semana p {
  font-size: 0.7rem;
  color: #666;
}
.card-resultado-semana header strong {
  display: inline-block;
  font-size: 0.8rem;
  margin-bottom: 10px;
}
.card-resultado-semana span {
  color: #ccc;
  font-size: 0.8rem;
}

.status {
  display: block;
  text-align: center;
  vertical-align: middle;
}

.status .material-icons,
.status {
  font-size: 0.8rem;
  color: #fff !important;
}
