.app {
  width: 100%;
  margin-top: 80px;
  padding: 0 20px;
}

.layer1 {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 50px;
}
.layer2 {
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;
}
.charts-container {
  width: 100%;
  max-width: 350px;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
}

.card-chart {
  flex: 1;
  max-width: 100%;
  padding: 20px;
  border-radius: 3px;
}
.card-chart header {
  margin-bottom: 30px;
  box-shadow: 0 -2px 12px rgba(225, 225, 225, 0.13);
}
.card-chart header p {
  color: #666;
}
.card-chart header h3 {
  font-size: 1.2rem;
  text-transform: uppercase;
}

.charts-container footer {
  border-top: 1px solid #ccc;
  padding: 5px 0;
  display: flex;
  align-items: center;
}
.charts-container footer span {
  color: #666;
  display: inline-block;
}
.resultado-semana-container {
  width: 100%;
  padding: 20px 30px;
  max-width: 350px;
  margin-bottom: 40px;
}
.resultado-semana-container .data-container {
  justify-content: flex-start;
}
.resultado-semana-container .data {
  display: block;
  font-size: 0.8rem;
  color: #666;
  margin-right: 10px;
}
.resultado-semana-container h3 {
  margin-bottom: 20px;
  text-transform: uppercase;
}

.resultado-semana-container .scrollable {
  height: 300px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  overflow-y: scroll;
  margin: 30px 0 0 0;
}

@media (min-width: 665px) {
  .app {
    justify-content: space-around;
  }
}

@media (min-width: 730px) {
  .layer1 {
    justify-content: space-between;
  }
}
.result-ai-container {
  width: 100%;
  max-width: 450px;
  border-radius: 3px;
  padding: 20px;
  margin-bottom: 40px;
}

.top-5-container {
  width: 100%;
  max-width: 450px;
}

.line-chart-container {
  margin: 30px 0;
  width: 100%;
  max-width: 450px;
  overflow: hidden;
}
.line-chart-container header {
  margin-bottom: 50px;
}
.legend {
  display: flex;
  align-items: center;
  margin: 5px 0;
}
.legend span {
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-right: 10px;
}

.legend span.bullet1 {
  background: #000;
}

.legend span.bullet2 {
  background: rgb(81, 45, 168);
}

@media (min-width: 730px) {
  .line-chart-container {
    max-width: 1000px;
  }
}
