.header-top {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 30px;
  background: #202124;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.16);
  z-index: 100;
}

.header-top h1 {
  color: #fff;
  font-size: 1.2rem;
  font-weight: 400;
}
