.input-fiuld {
  position: relative;
  margin-bottom: 44px;
}

.input-fiuld input {
  width: 100%;
  background: none;
  border: none;
  border-bottom: 1px solid var(--primary-color);
  padding: 10px 20px;
}

.input-fiuld label {
  transform: translateY(5px);
  position: absolute;
  left: 20px;
  transition: transform 0.3s, font-size 0.3s;
  font-size: 1.2rem;
  font-weight: 400;
}

.input-fiuld input:focus + label,
.not-empty + label {
  transform: translateY(-50%);
  font-size: 1rem;
}
.input-fiuld input:focus + label {
  color: var(--secundary-color);
}

.input-fiuld input[data-err]::after {
  content: attr(data-err);
  color: var(--err);
  display: block;
}
